<template>
  <div>
    <button id="J_btn_scanQR" class="btn btn-default"></button>
  </div>
</template>

<script>
import VConsole from 'vconsole'
import ap from '../alipayjsapi.inc.min'
export default {
    data(){
        return{
        }
    },
    mounted(){
        new VConsole();
        let btnScanQR = document.querySelector('#J_btn_scanQR');
        btnScanQR.addEventListener('click', function(){
            ap.scan(function(res){
                ap.alert(res.code);
            });
        });
    }
};
</script>

